/* <--------------------------- * SECTION1 * --------------------------- */
.switch_switchContainer {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 12.5rem;
    height: 8rem;
    padding-left: 1.3rem;
}

.switch_switchInput {
    height: 0;
    width: 0;
    visibility: hidden;
    &:checked {
        background: rgba($color: $primary, $alpha: 0.5);
        border: 0.1rem solid $background;
        transition: 0.3s;
        + .switch_switchLabel {
            &::after {
                left: 2rem;
            }
        }
    }
}

.switch_switchLabel {
    cursor: pointer;
    width: 3.8rem;
    height: 1.4rem;
    border-radius: 1.4rem;
    top: 3.9rem;
    left: 5rem;
    position: absolute;
    display: inline-block;
    border: 0.1rem solid $line_light;
    box-sizing: border-box;
    transition: 0.3s;
    &::after {
        content: '';
        position: absolute;
        height: 1.8rem;
        width: 1.8rem;
        left: -0.1rem;
        top: -0.3rem;
        border-radius: 1.8rem;
        background: $primary_600;
        box-shadow: $shadow_01dp;
        transition: 0.3s;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
