/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import '~/Constant/base.scss';
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin demo {
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.questionNode_container {
    border: 0.1rem solid $line_light;
}
.questionNode_bulkGirdTag {
    display: inline-block;
    width: 15.3rem;
    height: 3.7rem;
    line-height: 3.7rem;
    vertical-align: top;
    position: relative;
    box-sizing: border-box;
    border-right: 0.1rem solid $line_light;
}

.questionNode_bulkGirdTag::after {
    content: '';
    position: absolute;
    left: 0.2rem;
    top: 0;
    width: 100%;
    height: 2rem;
    border-bottom: 0.1rem solid $line_light;
    box-sizing: border-box;
    transform: rotateZ(13deg) scale(1.02);
}

.questionNode_bulkGirdTagRow {
    position: absolute;
    top: 1rem;
    left: 2rem;
}

.questionNode_bulkGirdTagCol {
    position: absolute;
    bottom: 0.8rem;
    right: 1rem;
}

.questionNode_bulkAdd {
    display: inline-block;
    font-size: 2rem;
    color: $primary_600;
    height: 3.7rem;
    vertical-align: top;
    line-height: 3.7rem;
    width: 5rem;
    text-align: center;
    cursor: pointer;
}

.questionNode_bulkHeader {
    display: flex;
    font-size: 1.4rem;
    border-bottom: 0.1rem solid $line_light;
    user-select: none;
    box-sizing: border-box;
}

.questionNode_bulkColOption {
    display: inline-block;
    position: relative;
    > div:first-child {
        display: inline-block;
        border-right: 0.1rem solid $line_light;
        height: 3.7rem;
        max-width: 7rem;
        padding-left: 1rem;
        padding-right: 1rem;
        line-height: 3.7rem;
        text-align: center;
        vertical-align: top;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}

.questionNode_bulkColContainer {
    width: calc(100% - 20.3rem);
    height: 3.7rem;
    vertical-align: top;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.questionNode_bulkRowOption {
    padding-left: 1rem;
    padding-right: 1rem;
    width: 15.3rem;
    height: 3.7rem;
    line-height: 3.7rem;
    text-align: center;
    vertical-align: top;
    border-right: 0.1rem solid $line_light;
    border-bottom: 0.1rem solid $line_light;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    box-sizing: border-box;
}

.questionNode_bulkRowAdd {
    width: 15.3rem;
    height: 3.7rem;
    line-height: 3.7rem;
    text-align: center;
    vertical-align: top;
    border-right: 0.1rem solid $line_light;
    box-sizing: border-box;
}
/* <--------------------------- * SECTION1 * --------------------------- */
