/* <--------------------------- * SECTION1 * --------------------------- */
.bulkAddOptionModal_container {
    position: relative;
    padding: 2.4rem;
    width: 60rem;
    height: 45.4rem;
    box-sizing: border-box;
}
.bulkAddOptionModal_title {
    @include h_2();
    color: $neutral_700;
}
.bulkAddOptionModal_content {
    margin-top: 3.2rem;
    &_title {
        @include h_4();
    }
    &_bulkContent {
        margin-top: 0.8rem;
        height: 23.2rem;
        border: 0.1rem solid $neutral_200;
        border-radius: 0.6rem;
        .scroll_scrollBody {
            padding-top: 1.6rem;
        }
    }
}
.bulkAddOptionModal_item {
    display: flex;
    padding-left: 1.6rem;
    padding-right: 0.8rem;
    justify-content: space-between;
}
.bulkAddOptionModal_first {
    display: flex;
    padding-left: 1.6rem;
    padding-right: 0.8rem;
    margin-top: 1.6rem;
    justify-content: space-between;
    textarea {
        width: 50rem;
        height: 19rem;
        outline: none;
        border: none;
        &::placeholder {
            @include body_3();
            color: $neutral_300;
        }
    }
    .bulkAddOptionModal_click {
        @include body_3();
        width: 50rem;
        height: 19rem;
        line-height: 2.4rem;
        color: $neutral_300;
        outline: none;
        border: none;
        cursor: pointer;
        user-select: none;
    }
}
.bulkAddOptionModal_no {
    @include body_3();
    color: $neutral_300;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2rem;
    height: 2.4rem;
}
.bulkAddOptionModal_btn {
    position: absolute;
    bottom: 1.6rem;
    right: 2.4rem;
    button {
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
