/* <--------------------------- * SECTION1 * --------------------------- */
.autoSaveSettingModal_container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 42.8rem;
    height: 29.1rem;
    text-align: center;
    background: $background;
    border-radius: 0.4rem;
}
.autoSaveSettingModal_title {
    padding-top: 2.6rem;
    font: $fs_r_24;
    color: $type;
    span {
        display: inline-block;
        height: 3.2rem;
    }
}
.autoSaveSettingModal_info {
    @include body_2();
    margin-top: 0.8rem;
    font: $fs_r_14;
    color: $gray_3;
}
.autoSaveSettingModal_countContainer {
    display: flex;
    align-items: center;
    margin-top: 3rem;
    margin-bottom: 0.6rem;
    user-select: none;
    > span {
        color: $gray_1;
    }
    svg {
        color: $primary_600;
    }
    .autoSaveSettingModal_count__disable {
        color: $line_light;
    }
}
.autoSaveSettingModal_count {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 8.1rem;
    height: 3.1rem;
    margin-right: 0.6rem;
    padding: 0 1rem;
    border: 0.1rem solid $not_activated;
    border-radius: 0.5rem;
    input {
        flex: 1;
        width: 100%;
        outline: none;
        color: $gray_2;
        text-align: center;
        display: inline-block;
    }
    span {
        display: flex;
        align-items: center;
        height: 100%;
        cursor: pointer;
    }
}
.autoSaveSettingModal_countInfo {
    font: $fs_r_14;
    color: $gray_4;
}
.autoSaveSettingModal_buttons {
    position: absolute;
    bottom: 2.5rem;
    text-align: center;
    button {
        font-size: 14px !important;
    }
    .button_button__primary {
        background: $primary_600;
    }
    .button_secondaryButton__big {
        margin-right: 2.4rem;
        color: $primary_600 !important;
        &:hover {
            color: $primary_600 !important;
            border-color: transparent !important;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
